@import url(https://dev-cats.github.io/code-snippets/JetBrainsMono.css);
:root {
  --background-color: #07051f;
  --black-white: rgba(255, 255, 255, .6)
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "JetBrains Mono", sans-serif;
  color: white;
  padding: 0;
  margin: 0;
}

html {
  font-size: 1vw;
  overflow-x: hidden;
  background: #07051f;
  background: var(--background-color);
}

section {
  height: 100vh;
  width: 100vw;
}

section:not(:first-child) > div:last-child {
  width: 80%;
  height: 90%;
}

div[id$="before"] {
  padding-bottom: 2%;
}

section h1 {
  font-size: 2rem;
  position: relative;
  left: 5vw;
  top: 2vh;
}

p {
  font-family: "JetBrains Mono", sans-serif;
}

.home {
    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.home > * {
    flex: 1 0;
    background: var(--background-color);
    height: inherit;
    font-family: "JetBrains Mono", sans-serif;
    overflow: hidden;
}

.home > .name {
    display: flex;
    align-items: stretch;
    flex-direction: column;
    text-align: left;
}

.name > p:first-child {
    margin-top: 26vh;
}

.home p {
    width: 60%;
    position: relative;
    left: 5vw;
    font-size: 3.2rem;
    color: white;
    display: flex;
    flex-direction: column;
}

.name-p {
    padding-bottom: 2vh;
}

.name-p ~ p, .name > p:first-child:before {
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.6);
}

.name > p:first-child:before {
    content: "// hello world";
    font-size: .9rem;
}

.home > .menu {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

#menu ul {
    position: fixed;
    right: 8%;
    top: 26%;
}

.picture {
    width: 100%;
    height: 80%;
    object-fit: cover;
    opacity: 1;
    position: relative;
    left: 12.5%;
}

.picture-code {
    position: relative;
    height: 18%;
    width: 40%;
    bottom: 10%;
    object-fit: scale-down;
}

.pics {
    height: 100%;
    width: 60%;
}

.picture,
.picture-code {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

#menu {
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 12%;
}

.menu-element {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: rgba(255, 255, 255, .6);
    font-size: 1.4rem;
    transition: all 200ms;
}

.menu-element:not(:first-child) {
    padding: .8vh 0;
}

.menu-element > p {
    color: rgba(255, 255, 255, .6);
    font-size: 1.4rem;
    transition: all 400ms;
    transform-origin: 70% 50%;
}

.menu-element:hover {
    text-decoration: line-through;
    color: white;
}



#portfolio {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex-wrap: nowrap;
}

#portfolio > h1 {
    flex: 1 0;
}

#portfolio > #projects {
    flex: 20 1;
}


#projects {
    width: 100%;
    display: flex;
    align-items: center;
}

#projects > .content {
    width: 90%;
    height: 90%;
    margin-left: 17vh;
}

.type-element {
    display: inline-block;
    color: var(--black-white);
    margin-right: 4%;
    transition: all 200ms;
    font-size: 1.4rem;
}

.type-element:hover {
    color: white;
}

.projects-grid {
    margin-top: 2%;
    height: 90%;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.projects-grid > .projects-name {
    flex: .7 1;
}

.projects-grid > * {
    flex: 1 1;
}


.card {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    padding: 2% 0;
}

.card > p {
    color: var(--black-white);
    transition: all 100ms;
}

.title {
    color: var(--black-white);
    font-size: 2.1rem;
    margin-bottom: 1%;
}

.card > p:last-child {
    font-size: 1.4rem;
}

.card:hover > p {
    color: white;
}






.info {
    padding-left: 20%;
    width: 80%;
    height: 100%;
}

.info > .projects-image {
    width: 100%;
    height: 50%;
    margin-bottom: 2vh;
}

.info p {
    color: var(--black-white);
}

.printing > p.title {
    color: white;
    font-size: 2rem;
}


.printing > * {
    display: inline-block;
}

.printing {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.cursor {
    width: 5px;
    height: 2rem;
    background: white;
    margin-left: 10px;
}

.blinking {
    animation: cursor-blink 1s forwards infinite;
}

@keyframes cursor-blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


.wrap.removing {
}

.experience {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.experience > div {
    height: 100%;
    width: 100%;
    flex: 1 1;
}

